body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  /*background: linear-gradient(#ff512f, #dd2476);*/
  background: darkred;
  text-align: center;
}
h2 {
  color: white;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: "Arial Black", Gadget, sans-serif;
  letter-spacing: 2px;
}

.comming {
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 3.5em;
  font-family: "Comic Sans MS", cursive, sans-serif;
}
.admin {
  color:white;
  display: block;
}
.admin input {
  margin: 10px;
}


